body {
    background-color: $gray;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrapper {
    max-width: 1200px;
    padding: 0 1rem; 
    display: grid;
    grid-template-rows: 100px, 1fr;

    @include respond-to("small") {
        padding: 0 1rem;
    }
}